.section {
    margin-top:70px;
    height: 100%;
    position: relative;

}

.container {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;

}
.block {
    height: 100px;
    width: auto;
}

.headLineContainer {
    place-items: center;
}
.headLine {
    font-size: 48px;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    line-height: 1.2;
    text-transform: uppercase;
    color: rgb(46, 45, 45);
    margin: 0;
}


